
	import XHeader from 'vux/src/components/x-header/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

	import mixins from 'assets/js/mixins.js'
	export default {
		name: 'contractDoctor',
		mixins: [mixins.scroller],
		data() {
			return {}
		},
		computed: {
			filterList() {
				return this.list.map((t) => {
					t.ServiceStar = parseFloat(t.ServiceStar);
					return t
				})
			},
			// 01
			isSign() {
				if(this.$route.query.departmentId) {
					return 0
				} else {
					return 1
				}
			},
			ajaxData() {
				return {
					header: {
						action: 'GetDoctorList',
					},
					body: {
//						userid: this.$route.query.userId || this.$common.getLocalStorage('user').userid,
						SignType: this.isSign,
						HospitalID: this.$route.query.hospitalId,
						DepartID: this.$route.query.departmentId,
						DoctorName: ''
					}
				};
			}
		},
		methods: {

		},
		mounted() {},
		components: {
			XHeader,
			Rater,
			Spinner
		}
	}



